.input:focus {
    outline: 1px #000000;
}

.input {
    border: 1px solid #d1d1d1;
    border-radius: 5px;
    width: 372px;
    padding: 10px 20px;
}

.inputWrapper {
    display: flex;
    flex-direction: column-reverse;
    row-gap: 5px;
}

.superInput {
    color: #000000;
}

.errorInput {
    border: 1px solid #cc1439;
}

.errorInput:focus {
    outline: 1px #cc1439;
}

.error {
    font-size: 10px;
    color: #cc1439;
}
