.button {
    cursor: pointer;
    display: block;
    padding: 10px 24px;
    font-size: 14px;
    font-weight: 600;
    border-radius: 3px;
    border: none;
    color: #ffffff;
}

.disabled {
    background-color: #004d99;
    opacity: 50%;
}

.secondary {
    border: 1px solid #0066cc;
    background-color: #ffffff;
    color: #0066cc;
}

.default {
    background-color: #0066cc;
}

.red {
    background-color: #cc1439;
}

